import React from "react";
import "./scss/Diya_player.scss";
import $ from 'jquery';
import Fullscreen from "react-full-screen";

function Player(video) {
    var isFull = true;
    var a;
    var b;
    var c = video.video;
    var protocol = window.location.protocol;
    var slashes = protocol.concat("//");
    var host = slashes.concat(window.location.hostname);
    console.log(c);
    a = $(window).width();
    if (a > 900) {
        a = 900;
    }
    b = a * (9 / 16);
    var tag = document.createElement('script');

    tag.src = "https://www.youtube.com/iframe_api";
    var firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    var player;
    window['onYouTubeIframeAPIReady'] = function () {
        player = new window.YT.Player('player', {
            height: a,
            width: b,
            videoId: c,
            playerVars: {
                controls: 0,
                showinfo: 0,
                rel: 0,
                disablekb: 1,
                modestbranding: 1,
                wmode: 'opaque',
                origin: host,
                data: 'small',
                theme: 'dark'
            },
            events: {
                'onReady': onPlayerReady,
                'onStateChange': onPlayerStateChange
            }
        });
    }
    function onPlayerReady(event) {
        $('.diyavideoplayer').show();
        $('#player').hide();
        $('.loader').hide();
        $(".diyapause").hide();
        event.target.playVideo();
        _diyau2beplayer_play()
        setTimeout(_diyau2beplayer_play(),3000)
    }
    function _diyau2beplayer_play() {
        player.playVideo();
    }

    function _diyau2beplayer_pause() {
        player.pauseVideo();
    }

    function _diyau2beplayer_seek_forward() {
        player.seekTo(player.getCurrentTime() + 10, true)
    }
    function _diyau2beplayer_seek_backward() {
        player.seekTo(player.getCurrentTime() + 10, true)
    }
    function _diyau2beplayer_fullscreen() {

        isFull = true;
    }
    return (
        <div>
            <Fullscreen
                enabled={isFull}
            >
                <div className="diyavideoplayer" style={{ height: b }}>
                    <div className="bg"></div>
                    <div className="player" id="player"></div>
                    <div className="loader"><img alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" /></div>
                    <div className="overlay"></div>
                    <div className="overlaycontrols">
                        <button onClick={_diyau2beplayer_seek_backward}> &#x23EA;</button>
                        <button className="diyaplay" onClick={_diyau2beplayer_play}>&#9658;</button>
                        <button style={{display:'none'}} className="diyapause" onClick={_diyau2beplayer_pause}>&#10074; &#10074;</button>
                        <button style={{display:'none'}} className="diyastop" onClick={_diyau2beplayer_seek_stop}>&#9608;</button>
                        <button onClick={_diyau2beplayer_seek_forward}> &#x23E9;</button>
                    </div>
                    <span className="fzb"
                        onClick={_diyau2beplayer_fullscreen}>[]</span>
                </div>
            </Fullscreen></div>
    )
    function _diyau2beplayer_seek_stop() {
        player.stopVideo();
    }
    function onPlayerStateChange(event) {
        if (event.data === 2) {
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 5) {
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 0) {
            $('#player').hide();
            $('.diyastop').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === -1) {
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 1) {
            $('#player').show();
            $('.diyastop').show();
            $('.loader').hide();
            $(".diyaplay").hide();
            $(".diyapause").show();

        } else if (event.data === 3) {
            $('.loader').show();
        } else {
            console.log(event);
        }
    }
}
export default Player;