	import React from 'react';
import Routes from './Eduzone_routes';
import { BrowserRouter as Router } from 'react-router-dom';
import Navbar from './Eduzone_nav';
window.Eduzone = "";

window.json_server = "/console/";
window.media_server = "/media/";
//window.json_server = "http://192.168.43.199/console/";
//window.media_server = "http://192.168.43.199/media/";
export default class Eduzone extends React.Component {
    render() {
        return (
            <Router basename="/classroom/">
                <Navbar />
                <div style={{minHeight:'85vh'}}>
                <Routes />
                </div>
            </Router>
        )
    }
}