import React from 'react';
import './scss/Eduzone_channel.scss';
import { Link } from "react-router-dom";
import logo from './img/icon.png'
class Channel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

            channel_loaded: false,
            Channel_data: []
        }
    }
    componentDidMount() {
        this.read_channels()
    }
    async read_channels(){
        await fetch(window.json_server +"channel/"+ this.props.match.params.id, {
            'cache': 'no-store'
        })
            .then(response => response.json())
            .then((jsonData) => {
                // jsonData is parsed json object received from url
                this.setState({
                    Channel_data: jsonData.data,
                    channel_loaded: true		
                })
            })
            .catch((error) => {
                // handle your errors here
                console.log(error)
            });
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }
    render() {
        return (
            <div>
                {this.state.channel_loaded === true ?
                    <div>
                        <div className="channel_profile">
                            <div className="twPc-div">
                                <div>

                                    <a title="Mert S. Kaplan" href="#a" className="twPc-avatarLink">
                                        <img alt="Mert S. Kaplan"
                                            src={logo}
                                            className="twPc-avatarImg" />
                                    </a>


                                    <div className="twPc-divStats">
                                        <div className="twPc-divUser">
                                            <div className="twPc-divName">
                                                <a href="#a">{this.state.Channel_data.name}</a>
                                            </div>
                                            <span>
                                                <a href="#a">@<span>{this.state.Channel_data.username}</span></a>
                                            </span>
                                        </div>
                                        <ul className="twPc-Arrange">
                                            <li key="0">
                                                <span>Videos</span>
                                                <span>0</span>
                                            </li>
                                            <li key="1">
                                                <span>Subscribers</span>
                                                <span>0</span>
                                            </li>
                                            <li key="2">
                                                <button>Subscribe</button>

                                            </li>
                                        </ul>
                                    </div>
                                </div>
                               <p style={{marginLeft:"10px"}}> Premium Channel </p>
                            </div>
                        </div>
                        <div className="channel_programmes">
                            <div className="row no-gutters">
                                {this.state.Channel_data.programmes.map(programmes => {
                                    return (
                                        <div className="col-6 col-sm-3 col-md-2">
                                            <Link to={"/channel/" + this.props.match.params.id + "/programme/" + programmes.uri}>
                                                <div className="card">
                                                    {programmes.name}
                                                </div>
                                            </Link>
                                        </div>

                                    )
                                })}
                            </div>
                        </div>
                    </div>
                    : null}
            </div>
        )
    }
}
export default Channel;