import React from 'react';

import { Route, Switch } from 'react-router-dom';
import Categories from './Eduzone_cat';
import Diyaads from './Diya_ads';
import Channel from "./Eduzone_channel";
import Programmes from "./Eduzone_programmes";
import Playlist from "./Eduzone_playlist";

class Routes extends React.Component {
    render() {

        return (
            <Switch>
                <Route exact path='/'
                    render={function () {
                        return (
                            <div>
                                <Diyaads/>
                                <Categories/>
                            </div>
                        )
                    }} />
                <Route exact path='/channel/:id' component={Channel}></Route>
                <Route exact path='/channel/:id/programme/:pgm' component={Programmes}></Route>
                <Route exact path='/channel/:id/programme/:pgm/u/:youtube_uri' component={Playlist}></Route>
                <Route
                    render={function () {
                        return (
                            <div>404 Not Found</div>
                        )
                    }} />
            </Switch>
        );
    }
}

export default Routes;