import React from 'react'
import {NavLink} from 'react-router-dom';

import { Link } from 'react-router-dom'
import "./scss/Eduzone_nav.scss"
import logo from "./img/logo_o.png"
//import icon from "./img/icon.png"

class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: 0
        }
    }
    handleDrawer = (states) => event => {
        if (
            event.type === "keydown" &&
            (event.key === "Tab" || event.key === "Shift")
        ) {
            return;
        }
        this.setState({ open: states });
    };
    closeMeNow() {
        navigator.app.exitApp();
    }
    render() {
        return (
            <div>
                <div className="diya_navbar">
                    <div className="navcontent">
                        <div className="menu-block"><button onClick={this.handleDrawer(1)}><i className="fa fa-reorder"></i></button></div>
                        <div className="logo-block">
                            <a href="#home">
                                <img alt="" src={logo} title="logo" />
                            </a>
                        </div>
                        <ul className="nav-block">
                            <li><i className="fa fa-search"></i></li>
                            <li><i className="fa fa-user"></i></li>
                        </ul>
                    </div>
                </div>
                <div className="diya_navbar-clear"></div>
                <div diya-role={this.state.open} className="diya_sidebar">
                    <div className="escape" onClick={this.handleDrawer(0)}></div>
                    <div className="sidebar_root" diya-animrole={this.state.open}>
                        <div className="sidebar">
                            <div className="titlees"><button onClick={this.handleDrawer(0)}><i className="fa fa-close"></i></button></div>
                            <ul>
                                <li><Link to="/" onClick={this.handleDrawer(0)}><i className="fa fa-home"></i>Home</Link></li>
                                <li><NavLink to="/courses" onClick={this.handleDrawer(0)}><i className="fa fa-certificate"></i>My Courses</NavLink></li>
                                <li><NavLink to="/profile" onClick={this.handleDrawer(0)}><i className="fa fa-user"></i>My Profile</NavLink></li>
                                <li><NavLink to="/payments" onClick={this.handleDrawer(0)}><i className="fa fa-exclamation-circle"></i>My Payments</NavLink></li>
                                <li><NavLink to="/help" onClick={this.handleDrawer(0)}><i className="fa fa-exclamation-circle"></i>Help</NavLink></li>
                                <li><NavLink to="/exit" onClick={this.handleDrawer(0)}><i className="fa fa-sign-out"></i>Exit</NavLink></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Navbar;